function SearchBox({ getSearchreResults, SearchAll, tokens, addresses }: ViewSearchProps) {
  const classes = useStyles()
  const classes1 = searchStyles()

  const fields = ['All Filters', 'Addresses', 'Tokens', 'Name Tags', 'Labels', 'Websites']
  const [searchBy, setSearchBy] = useState('All Filters')
  const [searchIndex, setsearchIndex] = useState('')

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>): Promise<void> => {
    e.preventDefault()
    getSearchreResults(searchIndex)
  }

  // useEffect(() => {
  //   SearchAll(searchIndex)
  // }, [searchIndex])

  const { getRootProps, getInputProps, getListboxProps, getOptionProps, groupedOptions } = useAutocomplete({
    id: 'use-autocomplete-demo',
    options: top100Films,
    getOptionLabel: (option) => option.title,
  })

  return (
    <form onSubmit={handleSubmit} className={classes.form}>
      <FormControl className={classes.selectForm}>
        <NativeSelect
          IconComponent={KeyboardArrowDownIcon}
          value={searchBy}
          onChange={(e) => setSearchBy(e.target.value as string)}
          input={<SearchSelect />}
          classes={{
            icon: classes.iconType,
          }}
        >
          {fields.map((field) => (
            <option key={field} value={field}>
              {field}
            </option>
          ))}
        </NativeSelect>
      </FormControl>
      <FormControl className={classes.inputForm}>
        {/* <FormControl className={classes.inputForm} {...getRootProps()}> */}
        {/* <div {...getRootProps()}> */}
        {/* <SearchInput
          {...getInputProps()}
          placeholder="Search by Address / Txn Hash / Block / Token"
          onChange={(e) => setsearchIndex(e.target.value)}
          value={searchIndex}
        /> */}
        {/* </div> */}
        {/* {groupedOptions.length > 0 ? (
          <ul className={classes1.listbox} {...getListboxProps()}>
            {groupedOptions.map((option, index) => (
              <li {...getOptionProps({ option, index })} onClick={() => setsearchIndex(option.title)}>
                {option.title}
              </li>
            ))}
          </ul>
        ) : null} */}
        <SearchInput
          placeholder="Search by Address / Txn Hash / Block / Token"
          value={searchIndex}
          onChange={(e) => setsearchIndex(e.target.value)}
        />
      </FormControl>
      <Button type="submit" variant="contained" className={classes.submitBtn} disableRipple>
        <i className="fa fa-search"></i>
      </Button>
    </form>
  )
}

const mapStateToProps = (state: AppState) => ({
  tokens: state.search.tokens,
  addresses: state.search.addresses,
})

export default connect(mapStateToProps, { getSearchreResults, SearchAll })(SearchBox)
